// 菜单 侧边栏
import util from '@/libs/util.js';
var role = util.cookies.get('role'); // 管理员菜单

var adminMenu = {
  path: '/course',
  title: '课程管理',
  icon: 'book'
}; // 教师菜单

var teacherMenu = {};
var aside = [];

if (role === 'ROLE_ADMIN') {
  aside.push(adminMenu);
}

if (role === 'ROLE_TEACHER') {
  aside.push(teacherMenu);
}

export default aside;